
html{box-sizing:border-box
}
*,*:before,*:after{margin:0;padding:0;box-sizing:inherit
}
body{font-family:-apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,Helvetica,Arial,sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol";color:#222;font-size:16px;line-height:1.4;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;padding:0;display:-webkit-box;display:-ms-flexbox;display:flex
}
h1{font-size:41.6px
}
h2{font-size:33.6px
}
h3{font-size:27.2px
}
h4{font-size:20px
}
h5{font-size:16px
}
h6{font-size:13.6px
}
p{font-size:16px;line-height:1.4
}
body{width:100%;height:100%;overflow:hidden
}
.page{position:fixed;top:0;left:0;padding:40px;width:100%;height:100%;background:#FFF;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-orient:vertical;-webkit-box-direction:normal;-ms-flex-direction:column;flex-direction:column;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;-webkit-box-align:center;-ms-flex-align:center;align-items:center;-webkit-transition:-webkit-transform 800ms ease;transition:-webkit-transform 800ms ease;transition:transform 800ms ease;transition:transform 800ms ease, -webkit-transform 800ms ease
}
.page .title{line-height:80px;text-align:center;color:rgba(255,255,255,0.9);text-shadow:0 0 6px rgba(0,0,0,0.2)
}
.page .pagebody{width:100%;max-width:1300px;height:100%;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-orient:vertical;-webkit-box-direction:normal;-ms-flex-direction:column;flex-direction:column;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;-webkit-box-align:center;-ms-flex-align:center;align-items:center
}
.flexRow{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-orient:horizontal;-webkit-box-direction:normal;-ms-flex-direction:row;flex-direction:row
}
.flexCol{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-orient:vertical;-webkit-box-direction:normal;-ms-flex-direction:column;flex-direction:column
}
.allCenter{-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;-webkit-box-align:center;-ms-flex-align:center;align-items:center
}
@media (max-width:430px){
.page{padding:10px 20px
}
.page .pagebody{overflow:visible
}
}
.entry{position:absolute;left:0;top:0;width:100%;height:100%;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-orient:vertical;-webkit-box-direction:normal;-ms-flex-direction:column;flex-direction:column;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;-webkit-box-align:center;-ms-flex-align:center;align-items:center
}
.entry .page{opacity:0;-webkit-transform:translateY(100%) translateZ(0);transform:translateY(100%) translateZ(0);-webkit-transition:background 300ms ease,-webkit-transform 800ms ease;transition:background 300ms ease,-webkit-transform 800ms ease;transition:transform 800ms ease,background 300ms ease;transition:transform 800ms ease,background 300ms ease,-webkit-transform 800ms ease
}
.entry .currentPage{opacity:1;-webkit-transform:translateY(0) translateZ(0);transform:translateY(0) translateZ(0)
}
.entry .prePage{opacity:1;-webkit-transform:translateY(-100%) translateZ(0);transform:translateY(-100%) translateZ(0)
}
.entry .nextPage{opacity:1;-webkit-transform:translateY(100%) translateZ(0);transform:translateY(100%) translateZ(0)
}
.slideUpIn-enter-active{box-shadow:0 0 20px rgba(0,0,0,0.4);-webkit-transform:translateY(0) translateZ(0);transform:translateY(0) translateZ(0);opacity:1
}
.slideUpIn-leave-active{box-shadow:0 0 20px rgba(0,0,0,0.4);-webkit-animation:slide-out 800ms ease;animation:slide-out 800ms ease
}
.slideDownIn-enter-active{box-shadow:0 0 20px rgba(0,0,0,0.4);-webkit-animation:slideDownIn-in 800ms ease;animation:slideDownIn-in 800ms ease
}
.slideDownIn-leave-active{box-shadow:0 0 20px rgba(0,0,0,0.4);-webkit-animation:slide-out 800ms ease;animation:slide-out 800ms ease
}
.fade-enter-active,.fade-leave-active{opacity:0;-webkit-animation:fade 800ms ease 300ms normal forwards;animation:fade 800ms ease 300ms normal forwards
}
.fade-enter,.fade-leave-active{opacity:0
}
@-webkit-keyframes slideUpIn-in{
0%{-webkit-transform:translateY(100%) translateZ(0);transform:translateY(100%) translateZ(0);opacity:.5
}
100%{-webkit-transform:translateY(0) translateZ(0);transform:translateY(0) translateZ(0);opacity:1
}
}
@keyframes slideUpIn-in{
0%{-webkit-transform:translateY(100%) translateZ(0);transform:translateY(100%) translateZ(0);opacity:.5
}
100%{-webkit-transform:translateY(0) translateZ(0);transform:translateY(0) translateZ(0);opacity:1
}
}
@-webkit-keyframes slideDownIn-in{
0%{-webkit-transform:translateY(-100%) translateZ(0);transform:translateY(-100%) translateZ(0)
}
100%{-webkit-transform:translateY(0) translateZ(0);transform:translateY(0) translateZ(0)
}
}
@keyframes slideDownIn-in{
0%{-webkit-transform:translateY(-100%) translateZ(0);transform:translateY(-100%) translateZ(0)
}
100%{-webkit-transform:translateY(0) translateZ(0);transform:translateY(0) translateZ(0)
}
}
@-webkit-keyframes slide-out{
0%{-webkit-transform:scale(1) translateZ(0);transform:scale(1) translateZ(0);opacity:1
}
100%{-webkit-transform:scale(.9) translateZ(0);transform:scale(.9) translateZ(0);opacity:.9
}
}
@keyframes slide-out{
0%{-webkit-transform:scale(1) translateZ(0);transform:scale(1) translateZ(0);opacity:1
}
100%{-webkit-transform:scale(.9) translateZ(0);transform:scale(.9) translateZ(0);opacity:.9
}
}
@-webkit-keyframes fade{
0%{opacity:0
}
100%{opacity:1
}
}
@keyframes fade{
0%{opacity:0
}
100%{opacity:1
}
}


